import React from 'react';
import Navigation from './components/navigation/navigation';
import Logo from './components/logo/logo';
import LinkForm from './components/linkform/linkform';
import Rank from './components/rank/rank';
import Particles from 'react-particles-js';
import './App.css';
import RecogitionFace from './components/recogitionFace/recogitionFace';
import Signin from './components/signin/signin';
import Register from './components/register/register';
import Clarifai from 'clarifai';

const app = new Clarifai.App({
  apiKey: '0b051cdb86454824b528766d97f83127',
});

const BackgroundParticle = {
  particles: {
    number: {
      value: 75,
      density: {
        enable: false,
        value_area: 800
      },
    },
  },
  interactivity: {
    detect_on: window,
    events:{
      onhover: {
        enable: true,
        mode: 'repulse'
      }
    }
  },
  modes: {
    repulse: {
      distance: 200
    }
  }
}
class App extends React.Component{
  constructor(){
    super();
    this.state = {
      input : '',
      imageUrl: '',
      box: {},
      route: 'signin',
      isSignedIn: true
    }
  }
  // calculate the box we need
  calculateFaceLocation = (data) =>{
    const clarifaiFace = data.outputs[0].data.regions[0].region_info.bounding_box;
    const image = document.getElementById('input-image');
    const width = Number(image.width);
    const height = Number(image.height);
    return {
      leftCol: clarifaiFace.left_col * width,
      topRow: clarifaiFace.top_row * height,
      rightCol: width - (clarifaiFace.right_col * width),
      bottomRow: height - (clarifaiFace.bottom_row * height)
    }
  }
  // display the box
  displayFaceBox = (box) =>{
    this.setState({box: box});
  }
  // set event with on
  onInputChange = (event) =>{
    this.setState({
      input: event.target.value,
    })
  }
  onSignIn = (routeChange) =>{
    if(routeChange === 'signout'){
      this.setState({isSignedIn: false});
    }
    else if(routeChange === 'register'){
      this.setState({isSignedIn: true});
    }
    else{
      this.setState({isSignedIn: true})
    }
    this.setState({route: routeChange});
  }
  onSubmit = () =>{
    // promise
    this.setState({imageUrl: this.state.input});
    app.models.predict(Clarifai.FACE_DETECT_MODEL, this.state.input).then(
    response =>{ this.displayFaceBox(this.calculateFaceLocation(response));  
    }).catch(err => console.log(err));
  }
    render(){
      return(
        <div>
              <Particles className="Particle"
                  params={BackgroundParticle} />
              <Navigation isSignedIn={this.state.isSignedIn} onSignin={this.onSignIn}/>
              {/* wrap in bracket to use JSX and JS */}
              {
                this.state.route === 'signout' ? 
                <div>
                  <Logo/>
                  <Rank/>
                  <LinkForm onSubmit = {this.onSubmit} onInputChange = {this.onInputChange}/>
                  <RecogitionFace box={this.state.box} url={this.state.imageUrl}/>
                </div>
                :(
                  this.state.route === 'register' ? <Register onSignIn = {this.onSignIn}/> : <Signin onSignIn = {this.onSignIn}/>
                )
              }
            {/* set the props in here to the children get state from parent as props */}
        </div>
      )
    }
}
export default App;